import React from "react";
import CollapseWrapper from "../common/collapse";
import PropTypes from "prop-types";

const SerialNumber = ({ children }) => {
    const countChildren = React.Children.map(children, (child, index) => {
        const count = {
            ...child.props, value: index + 1
        };
        return React.cloneElement(child, count);
    });

    return (
        <div>
            {React.Children.map(countChildren, (child) => <div>{child.props.value}. Компонент списка</div>)}
        </div>
    );
};
SerialNumber.propTypes = {
    children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node])
};

const ChildrenExercise = () => {
    return (
        <CollapseWrapper title="Упражнение">
            <p className="mt-3">
                У вас есть компоненты Списка. Вам необходимо к каждому из них
                добавить порядковый номер, относительно того, как они
                располагаются на странице. Вы можете использовать как{" "}
                <code>React.Children.map</code> так и{" "}
                <code>React.Children.toArray</code>
            </p>
            <SerialNumber>
                <Component />
                <Component />
                <Component />
            </SerialNumber>
        </CollapseWrapper>
    );
};

const Component = () => {
    return <div>Компонент списка</div>;
};

export default ChildrenExercise;
